<script>
  import {mapGetters} from 'vuex'
  import '@/assets/css/comment.css'
  import {postPromise} from '@/assets/public'

  export default {
    name: '',
    computed: mapGetters([
      'navList',
      'lineScheduleObj',
      'isLoading',
      'showQuit'
    ]),
    watch: {
      '$route' (to, from) {
        this.$store.commit('hideLoading')
      }
    },
    data() {
      return {
        isLoginShow: false,
        isLogin: true,
        getName: '',
        smSiName: '',
        isShowMask: false,
        UserLoadingShow: true,
        UserRegisterBoxShow: false,
        isDisabled: false,
        loginName: ''
      }
    },
    methods: {
      //
      Quit(){
        //sessionStorage
        let user = JSON.parse(sessionStorage.getItem('user'))
        if(!user){
          this.$store.commit('hideQuitButton')
          return
        }
        sessionStorage.removeItem('user');
        window.location.reload()
      },
      initData() {
        //
        var getSystemMenuInfo = {
          "loginUserID": "huileyou",
          "loginUserPass": "123",
          "isDelete": 0
        }
//        this.$store.commit('showLoading')
        this.$store.dispatch('initNavList', getSystemMenuInfo)
        .then(()=>{
//          this.$store.commit('hideLoading')
        })

      },
      //
      changeTaualType(id) {
        this.smSiName = id.sm_si_Name
      },
      //
      toPersonalCenter(){
        this.$router.push({name:'MyTourOrder'})
      }
    },
    created() {
      this.initData();
    },
    mounted() {
      let ul = this.$refs.mainNavWrapContentList;
      let lis = ul.children;
      lis[0].className = 'active'
      for(let i=0;i<lis.length;i++){
        lis[i].onclick=function () {
          for(var j=0;j<lis.length;j++){
            lis[j].className = ''
          }
          lis[i].className = 'active';
        }
      }

      if (sessionStorage.getItem('user')) {
        var InformetionObj = JSON.parse(sessionStorage.getItem('user'))
        this.isLogin = false;
        this.isLoginShow = true;
        this.loginName = InformetionObj.ui_Name
      }
    },
  }
</script>
<style scoped>
